import Vue from 'vue'
import Vuex from 'vuex'
import _ from 'lodash'

const server = process.env.NODE_ENV == 'production'?
'https://mighty-cove-40994.herokuapp.com':
'http://localhost:3000'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    error: '',
    success: false,
    inProcess: false,
    people: [],
    result: [],
    foundName: ''
  },
  mutations: {
    processStarted: (state) => {
      state.error = ''
      state.success = false
      state.inProcess = true
    },
    processFinished: (state, error = '') => {
        error? state.error = error : state.success = true
        state.inProcess = false
    },
    setResult: (state, keyword) => {
      const result: any = []
      for (const person of state.people) {
        if ((person['name'] as String).includes(keyword)) {
          result.push(person['id'])
        }
      }
      state.result = result
    },
    findNameById: (state: any, id: any) => {
      const person = _.find(state.people, {id}) as any
      state.foundName = person['name']
    },
    setPeople: (state, people) => {
      state.people = people
    },
    addChild: (state, child) => {
      console.log(child)
    },
    addPartner: (state, {partner, id}) => {
      (state.people as any).map((person: any) => {
        if (person.id === id) {
          person.partners.push(partner)
        }
      })
    }
  },
  actions: {
    addChild: async ({commit, dispatch}, {name, id1, id2, sd}) => {
      const childId = await dispatch('fetchData', {
        url: `${server}/person/${id1}/${id2}/${sd}/child`,
        method: 'POST',
        body: {name}
      })
      if (childId) commit('addChild', childId)
    },
    addPartner: async ({commit, dispatch}, {name, id}) => {
      const partnerId = await dispatch('fetchData', {
        url: `${server}/person/${id}/partner`,
        method: 'POST',
        body: {name}
      })
      if (partnerId) {
        const partner = {
          id: partnerId,
          name: name
        }
        commit('addPartner', {partner, id})
      }
    },
    setPeople: async ({commit, dispatch}) => {
      const people = await dispatch('fetchData', {url: `${server}/person/people`})
      if (people) commit('setPeople', people)
    },
    fetchData: async ({commit}, {url, method = 'GET', body = null}) => {
      commit('processStarted')
      const ops = Object()
      if (body) {
        ops.method = method
        ops.headers = { 'Content-Type': 'application/json' }
        ops.body = JSON.stringify(body)
      }
      const response = await fetch(url, ops)
      if (response.status != 200) {
        const error = response.text()
        commit('processFinished', error)
        return false
      }
      const data = await response.json()
      commit('processFinished')
      return data
    }
  },
  getters: {
    getPeople: state => state.people,
    getResult: state => state.result,
    getSuccess: state => state.success,
    getFoundName: state => state.foundName
  },
  modules: {
  }
})
