<template>
<div>
  <v-app-bar flat app>
    <v-textField
      ref="find"
      :append-icon="appendIcon"
      @click:append="clear()"
      hide-details
      single-line
      dense
      outlined
      v-model="keyword"
    />
  </v-app-bar>
  <PeopleList/>
</div>
</template>

<script>
import PeopleList from '@/components/PeopleList'
export default {
  name: 'home',
  data: () => ({
    addChildDialog: false,
    addPartnerDialog: false,
    id: null,
    name: null,
    sd: null,
    id1: null,
    id2: null,
    keyword: ''
  }),
  computed: {
    success() {
      return this.$store.getters.getSuccess
    },
    foundName() {
      return this.$store.getters.getFoundName
    },
    appendIcon: function() {
      return this.keyword? "mdi-close" : "mdi-magnify"
    }
  },
  watch: {
    // success(val) {
    //   if (val == true) {
    //     this.closeAddChildDialog()
    //     this.closeAddPartnerDialog()
    //   }
    // }
    keyword(val) {
      this.$store.commit('setResult', val)
    },
    foundName(val) {
      this.keyword = val
    }
  },
  methods: {
    addChild(event) {
      event.preventDefault()
      this.$store.dispatch('addChild', {
        name: this.name,
        id1: this.id1,
        id2: this.id2,
        sd: this.sd
      })
    },
    openAddChildDialog(id1, id2) {
      this.id1 = id1
      this.id2 = id2
      this.addChildDialog = true
    },
    closeAddChildDialog() {
      this.id1 = null
      this.id2 = null
      this.name = null
      this.addChildDialog = false
    },
    openAddPartnerDialog(id) {
      this.id = id
      this.addPartnerDialog = true
    },
    closeAddPartnerDialog() {
      this.id = null
      this.name = null
      this.addPartnerDialog = false
    },
    addPartner(event) {
      event.preventDefault()
      this.$store.dispatch('addPartner', {
        name: this.name,
        id: this.id
      })
    },
    clear() {
      this.keyword = ''
    },
    find(keyword) {
      this.keyword = keyword
    }
  },
  components: {
    PeopleList
  }
}
</script>
