<script>
export default {
    computed: {
        people() {
            return this.$store.getters.getPeople
        },
        result() {
            return this.$store.getters.getResult
        }
    },
    watch: {
        result(ids) {
            document.getElementsByClassName('person').forEach(el => el.setAttribute('hidden', 'hidden'))
            ids.forEach(id => document.getElementById(id).removeAttribute('hidden'))
        }
    },
    methods: {
        findNameById(id) {
            this.$store.commit('findNameById', id)
        }
    }
}
